// axios
import request from './request'
import qs from 'qs'
import m from "../assets/js/common";
import md5 from 'js-md5';

const http ={
    /**
     * methods: 
     * @param url 
     * @param params 
     */
    get(url,params){
        const config = {
            method: 'get',
            url:url
        };
        if(params) config.params = params;
        return request(config)
    },
    //
    post(url,params){
        // params.token = m.getCookie('mallToken')||null;
        // params.source = m.getCookie('mallSource')||null;
        const config = {
            method: 'post',
            url:url,
            headers:{
                'Content-Type':'application/json;charset=utf-8'
            },
            data:params
        };
        return request(config)
    },
    //formdata
    formpost(url,params){
        // params.token = m.getCookie('mallToken')?m.getCookie('mallToken'):null;
        // params.source = m.getCookie('mallSource')?m.getCookie('mallSource'):null;
        const config = {
            method: 'post',
            url:url,
            headers:{
                'Content-Type':'application/x-www-form-urlencoded'
            },
            data:qs.stringify(params)
        };
        return request(config)
    },
    //formdata
    loginpost(url,params){
        params.password = md5(params.password);
        const config = {
            method: 'post',
            url:url,
            headers:{
                'Content-Type':'application/x-www-form-urlencoded'
            },
            data:qs.stringify(params)
        };
        return request(config)
    },
    //
    upload(url,data){
        const config = {
            method: 'post',
            url:url,
            headers: {'Content-Type': 'multipart/form-data'},
            data:data
        };
        return request(config)
    },

};
//
export default http



